$node-radius: 2;
$army-radius: 1;
$edge-width: 5/9;

$font-stack: 'Courier New', Courier, monospace;
$font-weight: 900;

$white: #eeeeee;
$grey: #444444;

@mixin unselectable {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}